import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  mode: 'hash',
  base: '/market',
  routes: [
    {
      path: '/',
      name: 'Home',
      component: () => import('@/pages/Student/Index')
    },
    {
      path: '/Enterprise',
      name: 'Enterprise',
      component: () => import('@/pages/Student/Index')
    },
    {
      path: '/Student',
      name: 'Student',
      component: () => import('@/pages/Student/Index')
    }
  ]
})



